import React from 'react'
import Multiselect from 'multiselect-react-dropdown'
import { toyService } from '../services/toy.service.js'

export class ToyAdd extends React.Component {
  state = {
    toy: {
      name: '',
      price: '',
      labels: '',
      imgUrl: '',
    },
  }

  multiselectRef = React.createRef()

  resetLabelSelect() {
    this.multiselectRef.current.resetSelectedValues()
  }

  handleChange = ({ target }) => {
    const field = target.name
    let { value } = target
    if (target.name === 'imgUrl') value = target.files[0]
    else if (target.name === 'price') value = +value
    this.setState(prevState => ({
      toy: { ...prevState.toy, [field]: value },
    }))
  }

  handleLabelsChange = labels => {
    this.setState(prevState => ({
      toy: { ...prevState.toy, labels },
    }))
  }

  handleImgUpload = ({ target }) => {
    const file = target.files[0]
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => {
      const imgUrl = reader.result
      this.setState(prevState => ({
        toy: { ...prevState.toy, imgUrl },
      }))
    }
    reader.onerror = function (error) {
      console.log('Error: ', error)
    }
  }

  render() {
    const { toy } = this.state
    const labels = toyService.getLabels()
    const { onAddToy } = this.props

    return (
      <form
        className="toy-add"
        onSubmit={ev => {
          onAddToy(ev, toy)
          this.resetLabelSelect()
          this.setState({ toy: { name: '', price: '', labels: '' } })
        }}
      >
        <div className="form-group">
          <input
            required
            placeholder="Enter toy's name"
            value={toy.name}
            onChange={this.handleChange}
            id="name"
            type="text"
            name="name"
            autoComplete="no"
          />
          <label htmlFor="name">Name:</label>
        </div>
        <div className="form-group">
          <input
            required
            placeholder="Enter toy's price"
            value={toy.price}
            onChange={this.handleChange}
            type="number"
            name="price"
            autoComplete="off"
            id="price"
          />
          <label htmlFor="price">Price:</label>
        </div>
        <div className="form-group labels">
          <Multiselect
            isObject={false}
            onRemove={this.handleLabelsChange}
            onSelect={this.handleLabelsChange}
            options={labels}
            placeholder="Select labels from the list"
            id="labels"
            selectionLimit={3}
            ref={this.multiselectRef}
            onKeyPressFn={e => {
              e.key === 'Enter' && e.preventDefault()
            }}
          />
        </div>
        <div className="submit-container">
          <input
            type="file"
            id="file-input"
            onChange={this.handleImgUpload}
            name="imgUrl"
          />
          <label className="btn-add-img" htmlFor="file-input">
            Add image
          </label>

          <button className="btn-add-toy">Add Toy</button>
        </div>
      </form>
    )
  }
}
