import { toyService } from '../services/toy.service.js'
import { Link } from 'react-router-dom'
import React from 'react'

export class ToyDetails extends React.Component {
  state = {
    toy: null,
  }

  componentDidMount() {
    const { toyId } = this.props.match.params
    toyService.getById(toyId).then(toy => {
      if (!toy) {
        alert('Toy not found, returning to home page...')
        setTimeout(() => this.props.history.push('/toy'))
      }
      this.setState({ toy })
    })
  }

  render() {
    const { toy } = this.state
    if (!toy) return <h3>Loading...</h3>
    const { name, price, inStock, labels, reviews } = toy

    return (
      <main className="toy-details">
        <h2>Toy details</h2>
        <span>{name}</span>
        <p>Price: ${price}</p>
        <p>{inStock ? 'In' : 'Out of'} stock</p>
        <p>labels: {labels.length ? labels.join(', ') : ''}</p>
        <ul className="toy-reviews">
          {reviews.map((review, idx) => (
            <li className="toy-review" key={idx}>
              {review}
            </li>
          ))}
        </ul>

        <Link className="link-to-index" to="/toy">
          Back
        </Link>
      </main>
    )
  }
}
