import React from 'react'
import { utilService } from '../services/util.service.js'
import { toyService } from '../services/toy.service.js'
import Multiselect from 'multiselect-react-dropdown'

export class ToyFilter extends React.Component {
  state = {
    filterBy: {
      labels: [],
      name: '',
      inStock: 'all',
      sortBy: 'name',
    },
  }

  handleChange = ({ target }) => {
    const field = target.name
    this.setState(
      prevState => ({
        filterBy: { ...prevState.filterBy, [field]: target.value },
      }),
      () => this.debouncedFilterBy(this.state.filterBy)
    )
  }

  handleLabelsChange = labels => {
    this.setState(
      prevState => ({
        filterBy: { ...prevState.filterBy, labels },
      }),
      () => this.debouncedFilterBy(this.state.filterBy)
    )
  }

  debouncedFilterBy = utilService.debounce(this.props.onFilterBy, 500)

  render() {
    const { labels } = toyService.getLabels()

    return (
      <section className="toy-filter">
        <form>
          <label htmlFor="name">
            <input
              className="toy-search"
              autoComplete="off"
              placeholder="Search for toys"
              name="name"
              onChange={this.handleChange}
              type="search"
            />
          </label>

          <Multiselect
            isObject={false}
            onKeyPressFn={function noRefCheck() {}}
            onRemove={this.handleLabelsChange}
            onSelect={this.handleLabelsChange}
            options={labels}
            placeholder="Filter by label"
            id="labels"
            selectionLimit={3}
            ref={this.multiselectRef}
          />

          <label htmlFor="inStock">
            Filter by stock: {'  '}
            <select onChange={this.handleChange} name="inStock">
              <option value="all">All</option>
              <option value={true}>In stock</option>
              <option value={false}>Out of stock</option>
            </select>
          </label>

          <label htmlFor="sortBy">
            Sort: {'  '}
            <select onChange={this.handleChange} name="sortBy">
              <option value="name">A-Z</option>
              <option value="priceAscending">Price(low to high)</option>
              <option value="priceDescending">Price(high to low)</option>
            </select>
          </label>
        </form>
      </section>
    )
  }
}
