import axios from 'axios'

export const toyService = {
  query,
  getById,
  remove,
  save,
  getLabels,
}
const BASE_URL =
  process.env.NODE_ENV == 'production'
    ? '/api/toy'
    : 'http://localhost:3030/api/toy/'

const gLabels = [
  'On wheels',
  'Box game',
  'Art',
  'Baby',
  'Doll',
  'Puzzle',
  'Outdoor',
]

function query(filterBy, pageIdx) {
  return axios.get(BASE_URL, { params: { filterBy, pageIdx } }).then(res => {
    return res.data
  })
}
function getById(toyId) {
  return axios.get(BASE_URL + toyId).then(res => res.data)
}
function remove(toyId) {
  return axios.delete(BASE_URL + toyId).then(res => res.data)
}
function save(toy) {
  if (toy._id) {
    return axios.put(BASE_URL + toy._id, toy).then(res => res.data)
  } else {
    return axios.post(BASE_URL, toy).then(res => res.data)
  }
}

function getLabels() {
  return gLabels
}
