const initialState = {
  toys: [],
  // filterBy: {
  //   name: '',
  //   inStock: 'all',
  //   labels: [],
  // },
}

export function toyReducer(state = initialState, action) {
  let toys
  switch (action.type) {
    case 'SET_TOYS':
      return { ...state, toys: action.toys }
    case 'REMOVE_TOY':
      toys = state.toys.filter(toy => toy._id !== action.toyId)
      return { ...state, toys }
    case 'ADD_TOY':
      toys = [action.toy, ...state.toys]
      return { ...state, toys }
    // case 'FILTER_TOYS':
    //   const filterBy = { ...action.filterBy }
    //   return { ...state, filterBy }
    case 'UPDATE_TOY':
      toys = state.toys.map(currtoy =>
        currtoy._id === action.toy._id ? action.toy : currtoy
      )
      return { ...state, toys }
    default:
      return state
  }
}
