import { connect } from 'react-redux'
import React from 'react'
import { toyService } from '../services/toy.service.js'
// prettier-ignore
import { loadToys, removeToy, saveToy} from '../store/actions/toy.action.js'
import { setUserMsg } from '../store/actions/user.action.js'
import { ToyList } from '../cmps/toy-list.jsx'
import { ToyAdd } from '../cmps/toy-add.jsx'
import { ToyFilter } from '../cmps/toy-filter.jsx'

class _ToyApp extends React.Component {
  state = {
    pageIdx: 0,
    isAdding: false,
  }

  componentDidMount() {
    this.props.loadToys(undefined, 0)
  }

  onRemoveToy = toyId => {
    this.props.removeToy(toyId).then(() => {
      this.props.setUserMsg({
        type: 'success',
        txt: 'Todo removed successfully',
      })
    })
  }

  onAddToy = (ev, toy) => {
    ev.preventDefault()
    this.props.saveToy(toy)
  }

  onToggleAdd = () => {
    this.setState({ isAdding: !this.state.isAdding })
  }

  onToggleToy = toy => {
    this.props.toggleToy(toy)
  }

  onFilterBy = filterBy => {
    this.props.loadToys(filterBy, this.state.pageIdx)
  }

  onChangePage = pageChange => {
    let pageIdx = this.state.pageIdx + pageChange
    if (pageIdx < 0) return

    this.setState({ pageIdx }, () => this.props.loadToys(undefined, pageIdx))
  }

  render() {
    const { labels } = toyService.getLabels()
    const { toys } = this.props

    const { isAdding, pageIdx } = this.state
    return (
      <main className="toy-app">
        <ToyFilter onFilterBy={this.onFilterBy} labels={labels} />
        <div className={`toy-add-container ${isAdding ? 'active' : ''}`}>
          <button onClick={this.onToggleAdd} className="btn-open-add">
            Add new toy {isAdding ? '▲' : '▼'}
          </button>
          <ToyAdd labels={labels} onAddToy={this.onAddToy} />
        </div>
        <ToyList
          toys={toys}
          onRemoveToy={this.onRemoveToy}
          onAddToy={this.onAddToy}
        />
        <div className="pagination">
          <button
            disabled={pageIdx === 0}
            className="btn-prev"
            onClick={() => this.onChangePage(-1)}
          >
            Previous page
          </button>
          <button
            className="btn-next"
            // disabled={pageSize * pageIdx >= toys.length}
            onClick={() => this.onChangePage(1)}
          >
            Next page
          </button>
        </div>
      </main>
    )
  }
}

const mapStateToProps = storeState => {
  return {
    toys: storeState.toyModule.toys,
  }
}

const mapDispatchToProps = {
  loadToys,
  removeToy,
  saveToy,
  setUserMsg,
}

export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp)
