import { toyService } from '../services/toy.service.js'
import { saveToy } from '../store/actions/toy.action.js'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import React from 'react'

class _ToyEdit extends React.Component {
  state = {
    toy: {
      name: '',
      price: '',
      labels: '',
    },
  }

  componentDidMount() {
    const searchParams = new URLSearchParams(this.props.match.params)
    const toyId = searchParams.get('toyId')
    if (toyId) {
      toyService.getById(toyId).then(toy => this.setState({ toy }))
    }
  }

  handleChange = ({ target }) => {
    const field = target.name
    this.setState(prevState => ({
      toy: { ...prevState.toy, [field]: target.value },
    }))
  }

  onEditToy = (ev, toy) => {
    ev.preventDefault()
    this.props.saveToy(toy)
  }

  onGoBack = () => {
    this.props.history.push('/toy')
  }

  render() {
    const { toy } = this.state
    const { labels } = toyService.getLabels()

    const { name } = toy

    return (
      <section className="toy-edit ">
        <h2>Edit toy</h2>
        <form onSubmit={ev => this.onEditToy(ev, toy)}>
          <label htmlFor="name">
            Name:
            <input
              value={name}
              onChange={this.handleChange}
              type="text"
              name="name"
              autoComplete="off"
            />
          </label>

          <label htmlFor="labels">
            Labels:
            <input
              value={toy.labels}
              onChange={this.handleChange}
              type="text"
              name="labels"
              placeholder="Add labels seperated by commas"
              autoComplete="off"
              list="labels"
            />
          </label>
          <datalist id="labels">
            {labels.map((label, idx) => (
              <option key={idx} value={label}></option>
            ))}
          </datalist>

          <label htmlFor="price">
            Price:
            <input
              value={toy.price}
              onChange={this.handleChange}
              type="number"
              name="price"
              autoComplete="off"
            />
          </label>

          <button>Update</button>
          <Link to="/toy">Back</Link>
        </form>
      </section>
    )
  }
}

const mapStateToProps = storeState => {
  return {
    labels: storeState.toyModule.labels,
  }
}

const mapDispatchToProps = {
  saveToy,
}

export const ToyEdit = connect(mapStateToProps, mapDispatchToProps)(_ToyEdit)
