import { ToyPreview } from './toy-preview.jsx'

export function ToyList({ toys, onRemoveToy, onToggleToy }) {
  return (
    <section className="toy-list">
      {toys.map(toy => (
        <ToyPreview
          key={toy._id}
          toy={toy}
          onRemoveToy={onRemoveToy}
          onToggleToy={onToggleToy}
        />
      ))}
    </section>
  )
}
