import { toyService } from '../../services/toy.service.js'

// prettier-ignore
export function loadToys(filterBy = {name: '', inStock: 'all', labels: []},
pageIdx
) {
  return dispatch => {
    return toyService.query(filterBy,pageIdx).then(toys => {
      const action = {
        type: 'SET_TOYS',
        toys,
      }
      dispatch(action)
    })
  }
}

export function removeToy(toyId) {
  return dispatch => {
    return toyService.remove(toyId).then(() => {
      console.log('Deleted Succesfully!')
      dispatch({
        type: 'REMOVE_TOY',
        toyId,
      })
    })
  }
}

export function saveToy(toy) {
  return dispatch => {
    const actionType = toy._id ? 'UPDATE_TOY' : 'ADD_TOY'

    toyService.save(toy).then(savedToy => {
      dispatch({
        type: actionType,
        toy: savedToy,
      })
    })
  }
}

export function toggleToy(toy) {
  return dispatch => {
    toy.isDone = !toy.isDone
    toyService
      .save(toy)
      .then(savedToy => {
        console.log('Toggled toy', savedToy)
        dispatch({
          type: 'UPDATE_TOY',
          toy: savedToy,
        })
      })
      .catch(err => {
        console.error('Error:', err)
      })
  }
}
